.productLeft{
   
    flex-grow: 1;
    align-items: right;
}
.productRight{
    flex-grow: 2;
    
    padding-left: 20px;
    margin-left:-80px;
    min-width: 300px;
   
}

.productContainer{
   
    display: flex;
    width:100%;
    flex-flow: row
}

.product{
    font-family: input-mono,monospace;
}

.productTitle{
    margin-top: 20px;
    margin-left: 20px;
    font-size: 36px;
}
.productDescription{
    margin-top: 20px;
    margin-left: 20px;
    font-size: 18px;
}
.productPrice{
    margin-top: 30px;
    font-size: 28px;
}

.productSize{
    margin-top: 30px;
    font-size: 16px;
}

.callForAction{
    margin-top: 30px;
    font-size: 28px;
}